import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listProducts } from "../../actions/productActions";
import Loader from "../UI/Loader";
import Message from "../UI/Message";

const SaladesResponsive = () => {
  const dispatch = useDispatch();
  const productList = useSelector((state) => state.productList);
  const { error, loading, products } = productList;

  useEffect(() => {
    dispatch(listProducts());
  }, [dispatch]);

  return (
    <>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message type="error">{error}</Message>
      ) : (
        <div>
          {products
            .filter((item) => item.category === "salade")
            .map((filteredItem) => (
              <div
                key={filteredItem._id}
                className="product--responsive__container"
              >
                <div className="products--menu__image">
                  <img src={filteredItem.image} alt={filteredItem.name} />
                </div>
                <h2 className="product--responsive__title">
                  {filteredItem.name}
                </h2>
                <h3 className="product--responsive__price">
                  {filteredItem.price} €
                </h3>
              </div>
            ))}
        </div>
      )}
    </>
  );
};

export default SaladesResponsive;
