import React from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import logo from "../../images/logo.png";
import flag from "../../images/france.png";
import { GrFacebookOption } from "react-icons/gr";
import { FaPhoneAlt } from "react-icons/fa";
import { logout } from "../../actions/userActions";
import Button from "../UI/Button";

const Navbar = () => {
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  const dispatch = useDispatch();

  const logoutHandler = () => {
    // console.log("Logout");
    dispatch(logout());
  };

  return (
    <nav className="navbar">
      <div className="navbar--top">
        <div className="navbar--flag">
          <h2 className="navbar--flag__text">FR</h2>
          <div className="navbar--flag__icon">
            <img src={flag} alt="Frace Flag BioNems" />
          </div>
        </div>

        <Link to="/" className="logo--wrapper">
          <img src={logo} alt="Logo BioNems bionems" />
        </Link>

        <div className="navbar--contact__wrapper">
          {userInfo ? (
            <div className="navbar--dropdown__wrapper">
              <Button className="navbar--dropdown">
                {userInfo.name}
                <ul className="navbar--dropdown__list">
                  <li>
                    <Link className="navbar--dropdown__item" to="/profile">
                      Profile
                    </Link>
                  </li>
                  <li>
                    <Button
                      className="navbar--dropdown__item"
                      onClick={logoutHandler}
                    >
                      Logout
                    </Button>
                  </li>
                </ul>
              </Button>
            </div>
          ) : (
            <Link to="/login">Login</Link>
          )}
          {userInfo && userInfo.isAdmin && (
            <div className="navbar--dropdown__wrapper">
              <Button className="navbar--dropdown">
                Admin
                <ul className="navbar--dropdown__list">
                  <li>
                    <Link
                      className="navbar--dropdown__item"
                      to="/admin/userlist"
                    >
                      Users
                    </Link>
                  </li>
                  <li>
                    <Link
                      className="navbar--dropdown__item"
                      to="/admin/productlist"
                    >
                      Products
                    </Link>
                  </li>
                </ul>
              </Button>
            </div>
          )}

          <a
            className="navbar--top__facebook"
            href="https://www.facebook.com/Dbionems"
          >
            <GrFacebookOption />
          </a>
          <a href="https://wa.me/0664709183" className="navbar--top__phone-nr">
            <span className="navbar--phone-nr__icon-wrapper">
              <FaPhoneAlt />
            </span>
            06 64 70 91 83
          </a>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
