import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { listProducts } from "../../actions/productActions";
import NemsResponsive from "../../components/Products/NemsResponsive";
// import "../../styles/products/_products.scss";
import Loader from "../UI/Loader";
import Message from "../UI/Message";

const Nems = () => {
  const dispatch = useDispatch();
  // const [products, setProducts] = useState([]);
  const productList = useSelector((state) => state.productList);
  const { error, loading, products } = productList;

  const [width, setWidth] = useState(window.innerWidth);
  const breakpoint = 620;

  useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  useEffect(() => {
    dispatch(listProducts());
  }, [dispatch]);

  return width < breakpoint ? (
    <NemsResponsive />
  ) : (
    <>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message type="error">{error}</Message>
      ) : (
        <div className="products--menu">
          {products
            .filter((item) => item.category === "nem")
            .map((filteredItem) => (
              <Link
                to={`/product/${filteredItem._id}`}
                key={filteredItem._id}
                className="products--menu__item"
              >
                <div className="products--menu__image">
                  <img src={filteredItem.image} alt={filteredItem.name} />
                </div>
                <h2 className="products--menu__title">{filteredItem.name}</h2>
                <h3 className="products--menu__price">
                  {filteredItem.price} €
                </h3>
              </Link>
            ))}
        </div>
      )}
    </>
  );
};

export default Nems;
